/* eslint-disable eqeqeq */
/* eslint-disable no-console */
/* eslint-disable vue/require-prop-types */
import { mapGetters, mapActions, mapMutations } from 'vuex'
import Spinner from '@/components/filters/Spinner2'
export default {
  components: { Spinner },

  computed: {
    ...mapGetters(['getloadingGuests', 'allGuests', 'getSearch'])
  },

  watch: {
    getSearch: {
      handler (newVal) {
        this.loadGuestsPaginated()
      }
    }
  },

  mounted () {
    this.reset()
  },

  methods: {
    ...mapActions(['loadGuestsPaginated']),
    ...mapMutations(['RESET_SEARCH']),

    reset () {
      this.RESET_SEARCH()
      this.loadGuestsPaginated()
    },

    loadPaginationPage (url) {
      if (this.getloadingGuests) { return }
      if (!url) { return }
      this.loadGuestsPaginated(url)
    }
  }
}
